button{
  width: 250px;
  height: 80px;
  font-size: 35px;
  font-family: Roboto,sans-serif;
  font-weight: bold;
  color: #fff;
  border: 2px solid #282c34;
  border-radius: 15px;
  margin: 5px;
  cursor: pointer;
}

.boton-click:hover{
  background-color: #007c30;
}

.boton-click{
  background-color: #00b044;
}
.boton-reiniciar{
  background-color: #800011;
}
.boton-reiniciar:hover{
  background-color: #65000d;
}

